@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
*{
  -ms-overflow-style: none;
  scroll-behavior: smooth;
}
html::-webkit-scrollbar {
  display: none;
}
*::after,
*::before,
* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}
.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
}

.nav-item{flex:0 1 auto;padding:5px 0px}.nav-item:last-of-type{margin-right:0}.nav-item:hover{border-bottom:2px solid black;cursor:pointer}

.navbar{background-color:#F5DB13;padding:15px 10%;width:100%;display:flex;flex-flow:wrap row;justify-content:space-between;align-items:center;box-shadow:0px 2px 20px -6px #757070;z-index:3}.navbar .nav-items{font-family:'Karla';font-size:20px;list-style-type:none;display:flex;flex-flow:wrap row;width:57%;justify-content:space-between;align-items:center}.navbar a{text-decoration:none;color:black}.navbar .active{border-bottom:2px solid black}

.footer{display:flex;flex-flow:wrap row;padding:22px 10%;justify-content:space-between;align-items:center;width:100%;font-family:'Karla';flex-shrink:0;position:absolute;bottom:0}.footer div{display:flex;flex-flow:wrap row;align-items:center;font-weight:bold;font-size:18px}.red{color:red}

.home-content{flex:1 0 auto;width:100%;min-height:100%;background-image:linear-gradient(#F5DB13, #F2B907)}.home-content .banner{display:flex;flex-flow:wrap row;justify-content:space-between}.home-content .banner-info{flex:0 1 28%;margin-left:10%;margin-top:62px}.home-content .banner-info .banner-header{font-size:4em;line-height:0;letter-spacing:3.5px}.home-content .banner-info .banner-header .b-bold{font-weight:700}.home-content .banner-info .banner-header span{line-height:70px}.home-content .banner-info .banner-text{font-size:26px;margin-top:30px;line-height:30px}.home-content .banner-info .banner-button{margin-top:30px;width:220px;border:none;background-color:#73D677;border-bottom:12px inset #5EAF62;padding:10px;border-radius:10px;font-size:20px;font-weight:700}.home-content .banner-info .banner-button:hover{border-bottom-color:#73D677;background-color:#5EAF62;color:#F5DB13;cursor:pointer}.home-content .banner-info .banner-button:focus{outline:none}.home-content .banner-image{flex:0 1 47%;height:100%;min-height:600px;min-width:600px}.home-content .banner-image img{width:100%;height:90%;min-width:500px}

.e404-page{display:flex;flex-flow:nowrap column;width:100%;min-height:100vh;background-image:linear-gradient(to bottom, #D93E30, #D93E30);align-items:center}.e404-page .e404{font-size:640px;text-align:center;position:relative}.e404-page .e404 .text-404{font-family:'Karla';font-weight:bold;padding-top:50px;font-size:580px;line-height:480px;color:rgba(0,0,0,0.5);align-items:center;letter-spacing:4px}.e404-page .e404 .team-rocket{height:90%;width:50%;position:absolute;top:10%;margin-left:-270px}.e404-page .page-404-text{font-family:'Karla';font-weight:bold;font-size:40px;line-height:56px;margin-top:17px}.e404-page .page-404-text .first-text-error{color:white}.e404-page .page-404-text .second-text-error{color:black}.e404-page .e404-button{margin-top:50px;width:245px;border:none;background-color:#F2CB07;border-bottom:12px inset #F2B907;padding:10px;border-radius:15px;font-size:22px;font-weight:700}.e404-page .e404-button:hover{cursor:pointer;border-bottom-color:#F5DB13;background-color:#F2B907;color:white}.e404-page .e404-button:focus{outline:none}

.pokedex-content{width:100%;min-height:100%;max-height:100%;font-family:'karla';display:flex;flex-direction:column;align-items:center}.pokedex-content .title{margin-top:20px;text-align:center;font-weight:400;letter-spacing:3px}.pokedex-content .title span{font-weight:700}.pokedex-content .filters{margin-top:25px;width:75%;display:flex}

.search{width:75%;background-color:#F2F2F2;border-radius:20px;padding:15px 20px;margin:20px auto auto;border:none}.search:focus{outline:none;border:none}

.drop-down{transition:all ease-in-out .4s;width:130px;position:relative;margin-left:30px}.drop-down .drop-down-button{position:relative;display:flex;justify-content:center;width:100%;border:none;padding:4px 2px;background-color:#F2F2F2;border-radius:6px}.drop-down .drop-down-button:hover{cursor:pointer}.drop-down .drop-down-button .drop-down-icon{position:absolute;right:3px;top:7px}.drop-down:hover .filter-checks{opacity:1}.drop-down .filter-checks{opacity:0;width:100%;font-size:14px;margin-top:4px;border-radius:6px;position:absolute;left:0;z-index:55;background-color:#F2F2F2;padding-top:10px;box-shadow:3px 1px 6px -2px gray}.drop-down .filter-checks .check-item{display:flex;flex-flow:wrap row;margin:3px 0px 1px 25px}.drop-down .filter-checks .check-item input{margin-right:5px}.drop-down .filter-checks .check-item input:hover{cursor:pointer}.drop-down .filter-checks .check-item:hover{cursor:pointer;opacity:1}

.pagination-container{width:75%;margin-top:30px;min-height:55%;max-height:55%}.pagination-container .pokemons{width:100%;display:flex;flex-flow:wrap row;justify-content:space-between;align-content:space-between}

.single-pokemon{box-shadow:3px 3px 6px -3px grey;flex:0 1 30%;min-width:300px;display:flex;flex-flow:wrap row;border-radius:10px;margin-top:10px;transition:all ease-in-out .3s}.single-pokemon:hover{cursor:pointer;transform:scale(1.1);z-index:20}.single-pokemon .pokemon-info{flex:0 1 35%;padding:10px 0px 10px 20px;background-color:#F6F7F9;border-radius:10px}.single-pokemon .pokemon-info .pokemon-name{font-weight:bold;font-size:12px}.single-pokemon .pokemon-info .pokemon-qualification{margin-top:10px;display:flex;flex-flow:wrap row;justify-content:space-between}.single-pokemon .pokemon-info .qualification-text{font-size:11px;text-align:center;margin-top:5px}.single-pokemon .pokemon-info .circle{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:27px;padding:2px;border-radius:50%;border:2px solid black;display:flex;justify-content:center;align-items:center;font-size:13px}.single-pokemon .pokemon-info .pokemon-badges{font-size:11px;display:flex;flex-flow:wrap row;justify-content:space-between;width:135%;margin-top:5px}.single-pokemon .pokemon-info .pokemon-badges .badge{padding:0px 10px;border-radius:9px;z-index:15}.single-pokemon .pokemon-info .pokemon-badges .blue{background-color:#07D6F2}.single-pokemon .pokemon-info .pokemon-badges .green{background-color:#73D677}.single-pokemon .pokemon-image-container{width:65%;height:100%;border-top-right-radius:10px;border-bottom-right-radius:10px}.single-pokemon .spokemon-image{width:100%;height:100%;background-origin:border-box;background-position:center;background-clip:border-box;background-size:contain;background-repeat:no-repeat}

.u-bounce{animation:bounce 1000ms infinite}.u-flash{animation:flash 3000ms infinite}.u-rubber-band{animation:rubberBand 1200ms infinite}.u-flip{animation:flip 1400ms infinite;animation-timing-function:cubic-bezier(0.1, 0.67, 0.88, 0.34)}.u-tada{animation:tada 1200ms infinite}.u-pulse{animation:pulse 1000ms infinite}.u-swing{animation:swing 3000ms infinite}section article{border-top:solid 1px #545454;padding-bottom:30px}.u-text-center{text-align:center}.o-pokeball{width:54px;height:55px;border-radius:50px;border:solid 2px black;position:relative;background:linear-gradient(to bottom, #eee 0%, #fff 100%);margin:10px auto}.o-pokeball:before,.o-pokeball:after{content:"";display:block}.o-pokeball,.o-pokeball:before,.o-pokeball:after{transition:all 600ms cubic-bezier(0.67, 0.4, 0.36, 0.75)}.o-pokeball:before{width:50px;height:24px;border-bottom:solid 2px black;border-radius:25px 25px 0 0;background:linear-gradient(to bottom, #d10000 0%, red 50%)}.o-pokeball:after{content:"";width:10px;height:10px;background:linear-gradient(to bottom, #fff 0%, #ccc 100%);position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);border-radius:50%;box-shadow:0 0 0 1px black,0 0 0 2px #ddd,0 0 0 3.57143px black,0 0 5px 2.94118px rgba(0,0,0,0.4)}

.modal{display:flex;flex-flow:wrap row;justify-content:center;align-items:center;background-color:rgba(0,0,0,0.336);z-index:100;left:0;width:100%;height:100%;top:0;position:absolute}.dp-none{display:none}.pokemon-detail{width:55%;display:flex;flex-flow:wrap row;padding:20px 12px;position:relative}.pokemon-detail .close-pokemon-detail{right:0;position:absolute;right:-20px;top:-20px;font-size:30px}.pokemon-detail .pokemon-image-detail{padding:5px 3px;width:45%;border-top-left-radius:5px;border-bottom-left-radius:5px;box-shadow:2px 0px 6px 1px black}.pokemon-detail .pokemon-image-detail img{width:100%}.pokemon-detail .pokemon-modal-detail{width:55%;padding:10px 15px;border-top-right-radius:5px;border-bottom-right-radius:5px}.pokemon-detail .pokemon-modal-detail .pokemon-detail-header{font-family:'karla';display:flex;flex-flow:wrap row;justify-content:space-between;align-items:flex-end}.pokemon-detail .pokemon-modal-detail .pokemon-detail-header .pokemon-detail-name{color:white;font-size:32px;text-shadow:4px 4px 4px rgba(33,33,33,0.1)}.pokemon-detail .pokemon-modal-detail .pokemon-detail-header .pokemon-detail-gen{line-height:46px;font-size:20px;color:white}.pokemon-detail .pokemon-modal-detail .pokemon-detail-header .power-badge{background-color:#F2CB07;border-radius:50%;width:40px;height:40px;margin-left:5px;display:flex;flex-flow:wrap row;align-items:center;justify-content:center}.pokemon-detail .pokemon-modal-detail .pokemon-detail-header .generation-info{display:flex;flex-flow:wrap row}.pokemon-detail .pokemon-modal-detail .pokemon-abilities{padding:12px 30px;background:#FDFDFD;border-radius:5px;margin-top:30px;width:70%}.pokemon-detail .pokemon-modal-detail .experiences{display:flex;flex-flow:wrap row;justify-content:space-between;background:#FDFDFD;border-radius:5px;padding:20px 30px;margin-top:30px}.pokemon-detail .pokemon-modal-detail .pokemon-experiences{width:47%}.pokemon-detail .pokemon-modal-detail .pokemon-experiences .experience-bar{width:100%;height:7px;background-color:#EEE;border-radius:17px}.pokemon-detail .pokemon-modal-detail .pokemon-experiences .experience-bar .experience-fill-bar{width:70%;height:100%;border-radius:7px;background-color:lightgreen}.pokemon-detail .pokemon-modal-detail .abilities{display:flex;flex-flow:wrap row;margin-top:30px;justify-content:space-between}.pokemon-detail .pokemon-modal-detail .ability-container{padding:12px 25px;background-color:#FDFDFD;border-radius:5px}

.ability{display:flex;flex-flow:wrap column;justify-content:center;align-items:center}.qualification-text{font-size:11px;text-align:center;margin-top:5px}.circle{width:32px;height:32px;padding:2px;border-radius:50%;border:2px solid black;display:flex;justify-content:center;align-items:center;font-size:13px}

.legendaries-page{width:100%;background-color:#212121;height:100%;font-family:'karla';padding-bottom:50px}

.pokemon-collection-container{width:80%;margin:auto;color:white}.pokemon-collection-container:first-of-type{margin-top:20px}.pokemon-collection-container .collection-title{font-weight:200;font-size:72px;letter-spacing:4px;border-bottom:1px solid gray;padding-bottom:15px}

.pokemon-card{overflow:visible;width:140px;position:relative;padding-bottom:35px;background-image:linear-gradient(to right, #DDB74F, #FCE78B);border-radius:20px}.pokemon-card .card-image{z-index:10;border-top-left-radius:20px;border-top-right-radius:20px;position:relative}.pokemon-card .card-image img{width:110%;height:100%}.pokemon-card .not-active{background-color:#21212194;position:absolute;width:115%;height:100%;left:0;top:0;z-index:20}.pokemon-card .card-footer{background-color:#f7f7f7;display:flex;flex-flow:wrap row;justify-content:space-between;align-items:center;border-radius:20px;padding:20px 5px 10px 5px;position:absolute;bottom:0;left:0;right:0}.pokemon-card .card-footer h5{font-size:18px;color:black}.pokemon-card .card-footer .ball{width:20px;height:20px}.carousel-container{padding-bottom:50px}.alice-carousel{margin-top:40px;margin-bottom:50px}.alice-carousel__prev-btn{position:absolute;top:60px;left:-90px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.alice-carousel__prev-btn-item,.alice-carousel__next-btn-item{font-size:50px;color:white;font-size:72px;font-weight:700}.alice-carousel__prev-btn-item:hover,.alice-carousel__next-btn-item:hover{opacity:.7;color:#f7f7f7;transition:all ease-in-out 0.1;transform:scale(1.05)}.alice-carousel__next-btn{font-size:50px;position:absolute;right:-30px;top:60px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.alice-carousel__prev-btn-item.__inactive,.alice-carousel__next-btn-item.__inactive{opacity:1}

.pokemon-information{margin-top:20px;display:flex;flex-flow:wrap row;justify-content:space-between}.pokemon-information .information-detailed{flex:0 1 35%;padding-right:25px}.pokemon-information .information-detailed .pokemon-name-legendaries{font-size:60px;letter-spacing:4px;color:#FDFDFD}.pokemon-information .information-detailed .pokemon-specialists{display:flex;flex-flow:wrap row;justify-content:space-between}

.pokemon-experiences{width:47%}.pokemon-experiences .experience-bar{width:100%;height:7px;background-color:#EEE;border-radius:17px;overflow-x:hidden}.pokemon-experiences .experience-bar .experience-fill-bar{transition:all ease-in-out 1s;width:10%;height:100%;border-radius:7px;background-color:lightgreen;box-shadow:1px 0px 5px 1px #7b7b7b8a}

