@import '~animate.css/source/animate.css';

.u-bounce {
    animation: bounce 1000ms infinite;
    //animation-timing-function: cubic-bezier(0,1.28,0,1.27);
}

.u-flash {
    animation: flash 3000ms infinite;
}

.u-rubber-band {
    animation: rubberBand 1200ms infinite;
}

.u-flip {
    animation: flip 1400ms infinite;
    animation-timing-function: cubic-bezier(.1, .67, .88, .34);
}

.u-tada {
    animation: tada 1200ms infinite;
}

.u-pulse {
    animation: pulse 1000ms infinite;
}

.u-swing {
    animation: swing 3000ms infinite;
}


/*
 * Other configs
 */

section article {
    border-top: solid 1px #545454;
    padding-bottom: 30px;
}

.u-text-center {
    text-align: center;
}

/* Pokeball draw code */
// Size variable to keep all proportional if manipulate it
$size: 50px;

.o-pokeball {
    width: 54px;
    height: 55px;
    border-radius: $size;
    border: solid ($size/25) black;
    position: relative;
    background: linear-gradient(to bottom, #EEEEEE 0%, #FFFFFF 100%);
    ;
    margin: 10px auto;

    &:before,
    &:after {
        content: "";
        display: block;
    }

    &,
    &:before,
    &:after {
        transition: all 600ms cubic-bezier(.67, .4, .36, .75);
    }

    &:before {
        width: $size;
        height: ($size/2) -($size/25/2);
        border-bottom: solid ($size/25) black;
        border-radius: ($size/2) ($size/2) 0 0;
        background: linear-gradient(to bottom, #d10000 0%, #ff0000 50%);
    }

    &:after {
        content: "";
        width: $size/5;
        height: $size/5;
        background: linear-gradient(to bottom, #fff 0%, #ccc 100%);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        box-shadow: 0 0 0 ($size/50) black,
            0 0 0 ($size/25) #ddd,
            0 0 0 ($size/14) black,
            0 0 ($size/10) ($size/17) rgba(0, 0, 0, 0.4);
    }
}